.container{
    display: flex;
  align-items: center;
  justify-content: center;
}
input[type=text]{
    width:60%;
    border:2px solid #aaa;
    border-radius:4px;
    margin:20px ;
    outline:none;
    padding:8px;
    box-sizing:border-box;
    transition:.3s;
  }
  
  input[type=text]:focus{
    border-color:dodgerBlue;
    box-shadow:0 0 8px 0 dodgerBlue;
  }